import { Container } from 'inversify';
import { TYPES } from './di_types';

// Api route
import { IChinaMobileAPI } from './api/interface/IChinaMobile';
import { ChinaMobileAPI } from './api/ChinaMobile';

// Business logic
import { IOverviewService} from '../bl/backend/interface/IOverview';
import { OverviewService } from '../bl/backend/Overview';

// Repository
import { IOverviewRepository } from '../data_store/rdb/china_mobile/repository/overview/IOverviewRepository';
import { OverviewRepository } from '../data_store/rdb/china_mobile/repository/overview/OverviewRepository';

var myContainer = new Container();
// Api route binding
myContainer.bind<IChinaMobileAPI>(TYPES.IChinaMobileAPI).to(ChinaMobileAPI);

// Business logic binding
myContainer.bind<IOverviewService>(TYPES.IOverviewService).to(OverviewService);

// Repository
myContainer.bind<IOverviewRepository>(TYPES.IOverviewRepository).to(OverviewRepository);

export { myContainer };